// src/api/axiosClient.ts
import axios from "axios";

const axiosClient = axios.create({
  baseURL: process.env.NEXT_PUBLIC_API_URL,
  withCredentials: true,
});

// Interceptor request
axiosClient.interceptors.request.use(
  (config) => {
    // G·∫Øn token n·∫øu c√≥
    const token =
      typeof window !== "undefined" ? localStorage.getItem("token") : null;
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }

    // N·∫øu body l√† FormData => ƒë·ªÉ axios t·ª± set multipart/form-data
    if (config.data instanceof FormData) {
      // KH√îNG set Content-Type ·ªü ƒë√¢y,
      // ƒë·ªÉ axios t·ª± th√™m boundary ph√π h·ª£p
    } else {
      // N·∫øu kh√¥ng ph·∫£i FormData => g·ª≠i JSON
      config.headers["Content-Type"] = "application/json";
    }

    return config;
  },
  (error) => Promise.reject(error)
);

// Interceptor response
axiosClient.interceptors.response.use(
  (response) => response,
  (error) => {
    const status = error.response?.status;
    if (status === 401) {
      console.warn("Unauthorized - C√≥ th·ªÉ token h·∫øt h·∫°n");
    }
    return Promise.reject(error);
  }
);

export default axiosClient;
